
.loading_image {
  min-height: calc(100vh - 8rem);
  width: var(--main-width);
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  margin-top: 8rem;
  width: 40%;
  display: flex;
  align-items: center;
  background-color: rgb(45, 39, 39);
  box-shadow:none;
  
}
@media (max-width: 768px) {
  .loading_image {
    width: 80%;
    margin-top: 4rem;
  }
}